
$메인컬러 : #ff0000;

@mixin 함수() {
  background-color: #eee;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.red {
  color: $메인컬러;
}

// 셀렉터 대신 쓰는 nesting
// div.container {
//   h4 {
//     color: blue;
//   }
//   p {
//     color: green;
//   }
// }

.my-alert {
  @include 함수()
}

.my-alert-yellow {
  @extend .my-alert;
  background-color: #ffe591;
}

.my-alert p {
  margin-bottom: 0;
}